*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-font: "Roboto";
  --accent: #ff6b08;
  --secondary: #545454;
  --light-txt-color: #fff;
  --dark-txt-color: #000;
  --attention-txt-color: #ff001b;
  --card-inform-txt-color: #8c8c8c;
  --header-background-color: rgba(0, 0, 0, .56);
  --footer-background-color: #f7f7f7;
  --arrow-pagination-color: #f7f7f7;
  --votes-bg-color: #f7f7f7;
  --btn-background-color: #ff6b01;
  --main-background-color: #fff;
  --modal-background-color: #fff;
  --box-shadow: none;
  --box-shadow-card: 0 0 50px 5px #ff6b01;
  --items: 1;
  --gap: 20px;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

.darkmode {
  --main-background-color: #040922;
  --modal-background-color: #050c2c;
  --dark-txt-color: #ebeaea;
  --card-inform-txt-color: #ff6b08;
  --votes-bg-color: #545454;
  --footer-background-color: #201c29;
  --secondary: #fff;
  transition: var(--animation);
  --box-shadow: 0 0 50px 5px #b20b13;
  --box-shadow-card: 0 0 50px 5px #b20b13;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  font-style: normal;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: 100%;
  display: block;
}

a, button {
  cursor: pointer;
}

