.card-set {
  gap: var(--gap);
  flex-wrap: wrap;
  display: flex;
}

.card-set__item {
  flex-basis: calc((100% - var(--gap) * (var(--items)  - 1) ) / var(--items) );
}

.modal_btn {
  height: 44px;
  text-transform: uppercase;
  color: #000;
  transition: background-color, var(--animation);
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.modal_btn:hover, .modal_btn:focus, .modal_btn--selected {
  color: var(--light-txt-color);
  background-color: #ff6b01;
  border-color: #ff6b01;
  box-shadow: 0 8px 43px rgba(120, 99, 84, .6);
}

.modal_posterbox {
  position: relative;
}

.movieTrailerPlayBtn {
  width: 75px;
  height: 75px;
  color: #ff6b01;
  opacity: .9;
  transition: opacity var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.movieTrailerPlayBtn.hide {
  display: none;
}

.movieTrailerPlayIcon {
  fill: currentColor;
}

.movieTrailerPlayBtn:hover, .movieTrailerPlayBtn:focus {
