@keyframes movebtn {
  0% {
    opacity: .7;
    transform: translateY(0);
  }

  100% {
    opacity: 1;
    transform: translateY(-40px);
  }
}

.to-top.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.to-top--position {
  z-index: 4;
  position: fixed;
  bottom: 40px;
  right: 30px;
}

@media screen and (min-width: 768px) {
  .to-top--position {
    bottom: 60px;
    right: 4%;
  }
}

.to-top--style {
  color: var(--accent);
  background-color: var(--secondary);
  height: 35px;
  width: 35px;
  transition: all var(--animation);
  border: none;
  border-radius: 35%;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 25px;
  font-weight: 500;
  animation: movebtn 2s ease-in-out infinite alternate both;
  display: flex;
  box-shadow: 3px 7px 17px rgba(127, 65, 79, .15);
}

.to-top--style:hover, .to-top--style:focus {
  color: var(--dark-txt-color);
  background-color: var(--accent);
  opacity: 1;
  animation-play-state: paused;
}

@media screen and (min-width: 768px) {
  .to-top--style {
    width: 40px;
    height: 40px;
    font-size: 30px;
  }
}

@media screen and (min-width: 1280px) {
