.theme-mode-label {
  width: 71px;
  height: 28px;
  cursor: pointer;
  z-index: 0;
  background: #ebebeb;
  border-radius: 28px;
  margin-top: 15px;
  margin-left: auto;
  transition: all .3s;
  display: block;
  position: relative;
  box-shadow: inset 0 5px 15px rgba(0, 0, 0, .4), inset 0 -5px 15px rgba(255, 255, 255, .4);
}

@media screen and (min-width: 768px) {
  .theme-mode-label {
    margin-top: 0;
  }
}

.theme-mode-label:after {
  content: "";
  width: 26px;
  height: 26px;
  background: linear-gradient(180deg, var(--btn-background-color), #d30e18);
  border-radius: 26px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
}

.theme-mode-label svg {
  width: 17px;
  z-index: 100;
  position: absolute;
  top: 6px;
}

.theme-mode-label svg.sun {
  fill: #fff;
  transition: all .3s;
  left: 5px;
}

.theme-mode-label svg.moon {
  fill: #7e7e7e;
  transition: all .3s;
  left: 49px;
}

.theme-mode-checkbox {
  width: 0;
  height: 0;
  visibility: hidden;
}

.theme-mode-checkbox:checked + label {
  background: #242424;
}

.theme-mode-checkbox:checked + label:after {
  background: linear-gradient(#777, #340000);
  left: 70px;
  transform: translateX(-100%);
}

.theme-mode-checkbox:checked + label svg.sun {
  fill: #7e7e7e;
}

.theme-mode-checkbox:checked + label svg.moon {
  fill: #d70000;
}

.theme-mode-checkbox:checked + label + .background {
  background: #242424;
}

.theme-mode-checkbox:active:after {
  width: 37px;
}

